import React from 'react';
import classes from './Post.module.css';





const Post = (props:any) => {
  return (
    <div className={classes.item}>

      <img src='data:image/jpeg;base64,' alt='ocean' />
      {props.message} 
      <div>
        <span> like </span> { props.likesCount }
      </div>
      

    </div>



  )

}

export default Post;